import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        // 
        searchKey: "",

        // 
        cartGoods: [],

        // 
        selGoodsList: [],





        // 
        user: [{
            userId: "",
        }],



        // 
        consigneeList: [
            {
                id: "1",
                name: "",
                tel: "17812345678",
                address:
                    " 138  7  501 "
            },
            {
                id: "2",
                name: "",
                tel: "13100000000",
                address: " 50 "
            }
        ],

        // 
        consigneeInfo: {
            id: "1",
            name: "",
            tel: "17812345678",
            address: " 138  7  501 "
        },

        // 
        orderList: [],
    },

    mutations: {
        // 
        getUser(state,data) {
            console.log(data);
            // for(let i = 0; i < state.user.length; i++) {
            //     if()
            // }
        },

        // 
        setSearchKey(state, key) {
            state.searchKey = key
        },

        // 
        addNum(state, data) {
            state.cartGoods.forEach((item) => {
                if (item.goodsId === data.goodsId) {
                    item.num += 1;
                }
            })
        },

        // 
        subNum(state, data) {
            state.cartGoods.forEach((item) => {
                if (item.goodsId === data.goodsId) {
                    item.num -= 1;
                }
            })
        },

        // 
        delGoods(state, data) {
            for (let i = 0; i < state.cartGoods.length; i++) {
                if (state.cartGoods[i].goodsId === data.goodsId) {
                    state.cartGoods.splice(i, 1);
                }
            }
        },

        // 
        addGoods(state, data) {
            for (let i = 0; i < state.cartGoods.length; i++) {
                if (state.cartGoods[i].goodsId === data.goodsId) {
                    state.cartGoods[i].num += data.num;
                    return;
                }
            }
            state.cartGoods.push(data);
        },

        // 
        setConsInfo(state, data) {
            state.consigneeInfo = data;
        },

        //     
        addSelGoodsList(state, data) {
            state.selGoodsList = [];
            data.forEach(item => {
                state.cartGoods.forEach(i => {
                    if (item === i.goodsId) {
                        state.selGoodsList.push(i);
                    }
                })
            })
        },

        // 
        delSelGoods(state) {
            state.selGoodsList.forEach(item => {
                for (let i = 0; i < state.cartGoods.length; i++) {
                    if (item.goodsId === state.cartGoods[i].goodsId) {
                        state.cartGoods.splice(i, 1);
                    }
                }
            })
        },

        // 
        addOrderList(state) {
            let obj = {};
            obj.time = new Date();
            obj.goodsList = state.selGoodsList;
            state.orderList.unshift(obj);

        }
    },
    actions: {

    }
})
